import * as React from "react";
import ScatterPlot from './scatterPlot';
interface Props {
    width: number;
    height: number;
    info:any
    showMap:any
}
interface State {
    name: string,
    relationList: any[]
}
export default class AppCity extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {
            name: this.props.info.name,
            relationList: []
        }
    };
    showMap = () => {
        this.props.showMap();
    };
    render() {
        const { width, height} = this.props;
        return (
            <div style={{textAlign:"center"}}>
                <p style={{width: "100%",textAlign: "center"}}>
                    <span className="actionBackToMap" style={{fontSize: "15px"}} onClick={this.showMap} >{"<<"}</span>
                    <span style={{paddingRight: "100px", fontSize: "25px"}} > {this.state.name}</span>
                </p>
                <div style={{position: "absolute", top: "10px", left: "10px"}}>
                    <ul style={{textAlign:"left"}}>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                    </ul>
                    <p></p>
                </div>
                <svg className="container"
                     width={width} height={height}>
                    <ScatterPlot width={width} height={height} data={this.props.info.personData} />
                </svg>

            </div>
        );
    }
}



